.photo-of-me {
  grid-column: 1;
  grid-row: 1;
  display: flex;
  align-items: flex-end;
  justify-content: center;

  @media (max-width: 1000px) {
    justify-self: flex-end;
    width: 100%;
  }

  &__container {
    position: relative;
    width: 100%;
    height: 0;
    padding-bottom: 100%;
    cursor: pointer;
    overflow-y: hidden;

    @media (max-width: 1000px) {
      width: 50%;
      padding-bottom: 50%;
    }

    @media (max-width: 800px) {
      width: 60%;
      padding-bottom: 60%;
    }

    @media (max-width: 600px) {
      width: 70%;
      padding-bottom: 70%;
    }
  }

  .photo-1,
  .photo-2 {
    position: absolute;
    bottom: -6%;
    left: 0;
    width: 100%;
    height: 79%;
  }

  .photo-1 {
    background: url(../../img/photo-of-me-1.webp) center/cover;
  }

  .photo-2 {
    background: url(../../img/photo-of-me-2.webp) center/cover;
    visibility: hidden;
  }

  .hammer {
    background: url(../../img/hammer.webp) center/cover;
    position: absolute;
    left: 12%;
    top: 12%;
    width: 28%;
    height: 27%;
    visibility: hidden;
  }

  .hammer-active {
    visibility: visible;
    transform-origin: -50%;
    animation: hammer-about 0.7s forwards;

    @keyframes hammer-about {
      0% {
        transform: rotate(0deg);
      }
      25% {
        transform: rotate(-10deg);
      }
      100% {
        transform: rotate(20deg);
      }
    }
  }

  p {
    position: absolute;
    bottom: 20%;
    left: 0;
    font-size: 1vw;
    z-index: -1;

    @media (max-width: 1100px) {
      font-size: 0.9rem;
    }

    @media (max-width: 800px) {
      top: 20%;
    }

    @media (max-width: 500px) {
      font-size: 2.8vw;
    }
  }
}

.photo-kick-anim {
  .photo-1 {
    visibility: hidden;
  }
  .photo-2 {
    visibility: visible;
  }
}
